



















































import VueBase from '@/VueBase'
import { Component, Emit } from 'vue-property-decorator'
@Component({
  name: 'SearchBar',
})
export default class SearchBar extends VueBase {
  private searchType = ''
  private searchValue = ''
  @Emit('enter')
  private enterSearch() {
    return [this.searchType, this.searchValue]
  }
}
